import Step from "../../base/step/step.vue"
import Scroll from '../../base/scroll/scroll.vue'
export default {
  name: "component_name",
  components: {
    Step,
    Scroll
  },
  data() {
    return {
      isOk: true,
      goodCommon: '',
      goodsObj: {},
      shopName: '',
      keyWord: ''
    }
  },
  created() {
    //获取商品详情
    let that = this;
    this.$axios.post('/api/orderOperate/getAdditionalInfo', {
      'buyerTaskRecordId': that.$route.query.buyerTaskRecordId
    }).then((data) => {
      console.log(data)
      if (data.data.code === '200') {
        this.goodsObj = data.data.data
        this.shopName = this.goodsObj.shopName
        this.$nextTick(() => {
          this.$refs.scroll.refresh()
        })
      } else {
        this.$vux.alert.show({
          title: '获取信息失败',
          content: data.data.message,
        })
      }
    }).catch(function (err) {
      console.log(err)
    })
  },
  methods: {
    doNext() {
      let that = this;
      if (this.goodCommon !== '') {
        this.$axios.post('/api/orderOperate/backOrderSubmit', {
          buyerTaskRecordId: that.$route.query.buyerTaskRecordId,
          favorText: this.goodCommon
        }).then((data) => {
          console.log(data)
          if (data.data.code === '200') {
            this.$router.push({ name: 'submitSuccess', query: { type: 'evaluate1' } })
          } else {
            this.$vux.alert.show({
              title: '提交失败',
              content: data.data.message,
            })
          }
        }).catch((error) => {
          this.$vux.alert.show({
            title: '错误提示',
            content: '服务器错误',
          })
        })
      } else {
        this.$vux.alert.show({
          title: '提示',
          content: '请完善评论信息!',
        })
      }
    }
  }
}



